import { InjectionToken } from '@angular/core';

/**
 * Interface para dados de configuração do Version Tag
 * @interface
 */
export interface VersionTagConfig {

    /**
     * Propriedade que determina a versão da aplicação
     * @type {sring}
     */
    appVersion: string;
}

/**
 * Constante de configuração
 * @const
 */
export const VERSIONTAG_CONFIG = new InjectionToken<VersionTagConfig>('versiontag.config');
