import { InjectionToken } from '@angular/core';

/**
 * Interface para dados de configuração AuthConfig
 * @interface
 */
export interface AuthConfig {

    /**
     * Propriedade para URL base
     * @type {sring}
     */
    baseUrl: string;

    /**
     * Propriedade para URL de login
     * @type {sring}
     */
    loginUrl: string;

    /**
     * Rota de navegação quando o usuário for logado com sucesso na aplicação.
     * @type {sring}
     */
    loginSuccessRoute: string;

    /**
     * Propriedade para URL de logOut
     * @type {string}
     */
    logoutUrl: string;

    /**
     * Propriedade para URL de dados do usuário
     * @type {string}
     */
    detailsUrl: string;

    /**
     * Propriedade para definião de storage
     * @type {Storage}
     */
    storage: Storage;

    /**
     * Propriedade para identificar o idice do usuário no storage
     * @type {string}
     */
    userStorageIndex: string;

    /**
     * Propriedade para identificar o idice do token no storage
     * @type {string}
     */
    tokenStorageIndex?: string;

}

/**
 * Constante de configuração
 * @const
 */
export const AUTH_CONFIG = new InjectionToken<AuthConfig>('auth.config');
